  .card .card-content {
    background: white;
    display: inline-block;
    margin: auto;
    border-radius: 19px;
    position: relative;
    text-align: center;
    box-shadow: -1px 15px 30px -12px black;
    z-index: 9999;
    padding-bottom: 1rem; }
    .card .card-content .card-content__image {
      position: relative;
      margin-bottom: 1rem; }
      .card .card-content .card-content__image img {
        width: 100%;
        height: auto;
        border-top-left-radius: 14px;
        border-top-right-radius: 14px;
        background: #191919;
        border: #fff 4px solid; }
    .card .card-content .card-content__email {
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 700;
      margin-bottom: 3px; }
    .card .card-content .card-content__name {
      font-size: 1rem;
      color: black;
      font-weight: 900;
      margin-bottom: 5px;
      max-width: 170px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin: 0 auto; }

.cardlist {
